import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import { Link, useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../ClientSideDataTable'
import EditModal from './EditModal'
import PendingGroupList from './PendingGroupList'

const GroupList = () => {
    const AssignedMenus = JSON.parse(localStorage.getItem('AssignedMenus')) || []
    const Array2D = AssignedMenus.map(x => x.submenu.map(y => y.id))
    const subMenuIDs = [].concat(...Array2D)

    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [resetData, setReset] = useState(true)
    const [groupList, setgroupList] = useState([])
    const [groupInfo, setgroupInfo] = useState({})

    const [modal, setModal] = useState(false)
    const toggleModal = () => setModal(!modal)

    useEffect(() => {
        localStorage.setItem('useBMStoken', true)
        useJwt.getGroupList().then(res => {
            console.log(res)
            setgroupList(res.data)
            localStorage.setItem('useBMStoken', false)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err)
            setTableDataLoading(false)
            localStorage.setItem('useBMStoken', false)
        })
    }, [resetData])
    const handlePoPupActions = (id, message) => {
        localStorage.setItem('useBMStoken', true)
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                return useJwt.deleteGroup(id).then(res => {
                    Success(res)
                    console.log(res)
                    localStorage.setItem('useBMStoken', false)
                }).catch(err => {
                    localStorage.setItem('useBMStoken', false)
                    console.log(err.response)
                    Error(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {

            }
        })

    }
    const column = [
        {
            name: 'SL',
            minWidth: '20px',
            sortable: true,
            cell: (row, index) => index + 1  //RDT provides index by default
        },
        {
            name: 'Group Name',
            minWidth: '100px',
            sortable: true,
            selector: 'name'
        },
        {
            name: 'Is Default',
            minWidth: '100px',
            sortable: true,
            selector: Row => {
                return Row.isDefault ? 'True' : 'False'
            }
        },
        {
            name: 'Group Type',
            minWidth: '100px',
            sortable: true,
            selector: Row => {
                return Row.groupType === 1 ? 'Customer' : Row.groupType === 2 ? 'Agent' : 'Merchant'
            }
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Edit">
                        <Edit size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                setgroupInfo(row)
                                setModal(true)
                            }}
                        />
                    </span>&nbsp;&nbsp;
                    <span title="Delete">
                        <Trash size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 'Do you want to delete?')}
                        />
                    </span>
                </>
            }
        }
    ]
    const [activeTab, setActiveTab] = useState('1')

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

    return (
        <Card>
          <CardBody className='pt-2'>
            <Nav pills>
              <NavItem>
                <NavLink active={activeTab === '1'} onClick={() => toggle('1')}>
                  <span className='align-middle d-none d-sm-block'>Groups</span>
                </NavLink>
              </NavItem>
              {subMenuIDs.includes(27) && <NavItem>
                <NavLink active={activeTab === '2'} onClick={() => toggle('2')}>
                  <span className='align-middle d-none d-sm-block'>Approve Groups</span>
                </NavLink>
              </NavItem>}
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId='1'>
                <Card>
                    <CardHeader className='border-bottom'>
                        <CardTitle tag='h4'>Groups</CardTitle>
                        {subMenuIDs.includes(26) && <Button.Ripple className='ml-2' color='primary' tag={Link} to='/createGroup' >
                        <div className='d-flex align-items-center'>
                                <Plus size={17} style={{marginRight:'5px'}}/>
                                <span >Create Group</span>
                        </div>
                        </Button.Ripple>}
                    </CardHeader>
                    <CardBody>
                        <Row>
                            <Col md='12'>
                                <CommonDataTable column={column} TableData={groupList} TableDataLoading={TableDataLoading} />
                            </Col>
                        </Row>
                        <EditModal
                            toggleModal={toggleModal}
                            modal={modal}
                            resetData={resetData}
                            setReset={setReset}
                            groupInfo={groupInfo}
                            setgroupInfo={setgroupInfo}
                        />
                    </CardBody>
                </Card>
              </TabPane>
              <TabPane tabId='2'>
                <PendingGroupList />
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
    )
}

export default GroupList