import { Fragment, useState, useEffect, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical,
    Edit, Archive, Trash, Search, ChevronLeft, Eye, XCircle, Facebook, Globe, Instagram, Twitter
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, CardBody, CustomInput, Table, Spinner, InputGroup, InputGroupAddon, InputGroupText, FormFeedback, Progress, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import { useHistory } from 'react-router-dom'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'

const EditModal = ({ modal, toggleModal, groupInfo, setgroupInfo, setReset, resetData }) => {
    const [editPointRuleloading, seteditPointRuleloading] = useState(false)
    const handleChange = (e) => {
        setgroupInfo({ ...groupInfo, [e.target.name]: e.target.value })
    }

    const onSubmit = (e) => {
        e.preventDefault()
        const { id, name, groupType, isDefault } = groupInfo
        localStorage.setItem('useBMStoken', true)
        seteditPointRuleloading(true)
        console.log({ id, name, groupType, isDefault })
        useJwt.editGroup(id, { id, name, groupType, isDefault }).then(res => {
            setReset(!resetData)
            seteditPointRuleloading(false)
            console.log(res)
            toggleModal()
            Success(res)
        }).catch(err => {
            seteditPointRuleloading(false)
            localStorage.setItem('useBMStoken', false)
            Error(err)
            console.log(err)
        })
    }
    return (
        <Modal isOpen={modal} toggle={toggleModal} className='modal-dialog-centered'>
            <ModalHeader toggle={toggleModal}>Edit Group</ModalHeader>
            <ModalBody>
                <Form className="row" style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="serviceId">Group Name</Label>
                            <Input type="text"
                                name="name"
                                id='name'
                                value={groupInfo.name}
                                onChange={handleChange}
                                required
                                placeholder="group name"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="groupType">Group Type</Label>
                            <Select
                                theme={selectThemeColors}
                                maxMenuHeight={200}
                                className='react-select'
                                classNamePrefix='select'
                                value={{ value: groupInfo.groupType, label: groupInfo.groupType === 1 ? 'Customer' : groupInfo.groupType === 2 ? 'Agent' : groupInfo.groupType === 3 ? 'Merchant' : '' }}
                                onChange={(selected) => {
                                    setgroupInfo({ ...groupInfo, groupType: selected.value })
                                }}
                                options={[{ value: 1, label: 'Customer' }, { value: 2, label: 'Agent' }, { value: 3, label: 'Merchant' }]}
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="12" className='mb-1'>
                            <FormGroup check>
                                <Input onChange={(e) => {
                                        if (e.target.checked) { 
                                            setgroupInfo({ ...groupInfo, isDefault: true })
                                        } else {
                                            setgroupInfo({ ...groupInfo, isDefault: false })
                                        }
                                    }
                                } type='checkbox' id='isDefault' checked={groupInfo.isDefault}/>
                                <Label for='isDefault' check>
                                    Is Default?
                                </Label>
                            </FormGroup>
                        </Col> 

                    <Col sm="12" className='text-center'>
                        {
                            editPointRuleloading ? <Button.Ripple color='primary' className='mr-1' disabled style={{ marginTop: '25px' }}>
                                <Spinner color='white' size='sm' />
                                <span className='ml-50'>Loading...</span>
                            </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit" style={{ marginTop: '25px' }}>
                                <span >Update</span>
                            </Button.Ripple>
                        }
                    </Col>
                </Form>
            </ModalBody>
        </Modal>
    )
}
export default EditModal